.foudation{
    padding: 24px 40px;
    width: 400px;
    height: 500px;
    background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4F2EE8 18.03%, #FFFFFF 100%);
    box-shadow: 0px 10px 10px 0px #F5F5F71A;
    border-radius: 16px;
}

.foudation__title{
    margin-bottom: 40px;
    
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;

    letter-spacing: -0.04em;
    color: #FFFFFF;
}


.foundation__btn{
    display: block;
    margin: 0 auto;
    width: 38px;
    height: 38px;
    
    background: #FFFFFF;
    border: none;
    border-radius: 100%;
    transform: rotate(90deg);
}